import { mapGetters } from 'vuex';

export default {
  name: 'Progresso',
  data() {
    return {
      percento: 0
    };
  },
  mounted() {
    this.percento = this.calculaProgresso();
  },
  computed: {
    ...mapGetters(['dados'])
  },
  methods: {
    calculaProgresso() {
      return Math.round((this.dados.pontos * 100) / this.dados.metaTotal);
    }
  }
};
